<h3 id="fang-fa"><a class="header-anchor" href="#fang-fa" aria-hidden="true"></a> </h3>
<p> <code v-pre="">Message</code>  <code v-pre="">this.$message</code>  Message  <code v-pre="">close</code> </p>

</section></template>

<script>
  export default {
    methods: {
      open1() {
        this.$message('');
      },
      
      open2() {
        this.$message({
          message: '',
          type: 'success'
        });
      },

      open3() {
        this.$message({
          message: '',
          type: 'warning'
        });
      },

      open4() {
        this.$message.error('');
      },
      
      open5() {
        this.$message({isButton:true,message:''});
      },
      
      open6() {
        this.$message({
          isButton:true,
          message: '',
          type: 'success',
          buttonText:['',''],
          onClose:(data)=>{
            //buttonText
            console.log(data)
          }
        });
      },

      open7() {
        this.$message.error({
          isButton:true,
          message:'',
          buttonText:['',''],
          onClose:(data)=>{
            //buttonText
            console.log(data)
          }
        });
      }
    }
  }
</script>


